.ingredient-details {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  width: 520px;
}
.composition-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  gap: 20px;
}
.element {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
  width: 120px;
  height: 56px;
}
.description {
  text-align: center;
}
